const mongoose = require('mongoose');
const {
    ObjectId
} = mongoose.Schema;


const UserSchema = mongoose.Schema({
    name: {
        type: String,
        trim: true,
        required: true,
    },
    email: {
        type: String,
        trim: true,
        required: true,
    },
    photo: {
        data: Buffer,
        contentType: String,
    },
    password: {
        type: String,
        required: true,
    },
    created: {
        type: Date,
        default: Date.now(),
    },
    following: [{
        type: ObjectId,
        ref: 'User',
    }, ],
    followers: [{
        type: ObjectId,
        ref: 'User',
    }, ],
    role: {
        type: Number,
        default: 0,
    },
    type: {
        type: String,
        default: 'local',
    },
    updated: Date,
});

const model = mongoose.model("User", UserSchema);

export default model;